header {
  display: flex;
  padding: 32px 0 0 0;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
}

.block {
  width: 100%;
  line-height: 28px;
  font-size: $fontSize;

  font-family: "basier_circlesemi";
  a {
    text-decoration: none;
    color: $bodyColor;
    padding: 0 32px 0 0;
    font-size: $fontSize;
  }
}

.block_child {
  display: flex;
  a {
    text-decoration: none;
    color: $bodyColor;
    font-family: "basier_circleregular";
    padding: 0 32px 0 0;
    font-size: $fontSize;
  }
  a:hover {
    cursor: pointer;
  }
}

.side_text_left {
  font-family: "basier_circleregular";
  font-size: $fontSize;
  margin: 0;
  position: fixed;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  transform: rotate(-90deg);
  left: -20px;
  text-align: center;
  z-index: -1;
}

.side_text_right {
  font-family: "basier_circleregular";
  font-size: $fontSize;
  margin: 0;
  position: fixed;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  transform: rotate(90deg);
  right: -20px;
  text-align: center;
  z-index: -1;
}

@media only screen and (max-width: 728px) {
  header {
    display: flex;
    padding: 16px 0 0 0;
  }
  .side_text_left,
  .side_text_right {
    display: none;
  }
  .block_child :nth-child(3){
    display: none;
  }
}

